.next-project-text {
    display: block;
    @include paragraph-large($font-sans-200, center, 200);
    margin-bottom: 20px;
    span {
        @include paragraph-large($font-sans-400, center, 400);
    }
}

.connector-line {
    width: 50%;
    height: 20vw;
    margin-bottom: 120px;
    border-right: 2px solid $primary-2;
    @media only screen and (min-width:834px) {
        height: 10vw;
        margin-bottom: 240px;
    }
}

.next-project-margin {
    @media only screen and (min-width:834px) {
        margin-bottom: 800px;
    }
    @media only screen and (min-width:1200px) {
        margin-bottom: 900px;
    }
}

.next-project {
    overflow: hidden;
    max-width: 300px;
    margin: 0 auto;
    padding: 20px 60px 0 60px;
    @media only screen and (min-width:834px) {
        z-index: -2;
        position: fixed;
        // bottom: -800px;
        bottom: 0;
        left: 0;
        right: 0;
        top: 0;
        // height: 1200px;
        height: 100vh;
        max-width: 1200px;
    }
    @media only screen and (min-width:1200px) {
        // bottom: -700px;
        bottom: 0;
    }
    picture {
        display: block;


        transition: all .2s ease-in-out;
        img {
            width: 100%;
        }
    }
    @media only screen and (min-width:834px) {
        &:hover picture {
            transform: translateY(-60px);
        }
    }
    a {
        display: block;
    }
}